import React from "react";
import './ProjectPage.css';
import cover from "../../images/projects/fullstack-cover.png"

import exploreSchemas from "../../images/projects/SuperAssessor/explore-schemas.png"
import gameMain from "../../images/projects/SuperAssessor/game-main.png"
import gamemode from "../../images/projects/SuperAssessor/gamemode-select.png"
import missioncards from "../../images/projects/SuperAssessor/missioncard-select.png"
import mySchemas from "../../images/projects/SuperAssessor/my-schemas.png"
import replaceCard from "../../images/projects/SuperAssessor/replace-card.png"


const SuperAssessor = () => {
    return (
        <div className="project-page">
            <h1>Super Assessor</h1>
            <section className="project-text">
                <img src={cover} alt="example of the game board provided in exam text" /><br />
                Dette prosjektet ble lagd som en del av eksamen i faget Full-Stack webutvikling i Vr 2024. Vi var en gruppe p 3 stykker.
                Oppgaven vr var  lage en digital versjon av Super Assessor spillet, med diverse nye funksjoner.
                Den opprinnelige fysiske versjonen av spillet er et kortspill hvor undervisere samler kort og bruker dem til  lage nye vurderingsmetoder.
                Alle p gruppen jobbet p forskjellige deler av prosjektet og gjorde arbeid p frontend, backend, og databasen. Vi jobbet ogs alle sammen p figma prototypene vi lagde.
            </section>

            <section className="project-text">
                <h2>Nkkelfunksjoner</h2>
                <ul>
                    <li>Spillbart Super Assessor spill alene eller i gruppe</li>
                    <li>Etter fullfrt spill kan skjema lagres og deles med andre p nettsiden</li>
                    <li>Brukere kan vurdere og bookmarke andres skjema</li>
                    <li>Trygt loginsystem med roller og protected routes</li>
                    <li>Admin dashboard hvor du kan gjre CRUD funksjoner p kortene</li>
                </ul>
            </section>

            <section className="project-text">
                <h2>Mine bidrag:</h2>
                <ul>
                    <li>Jeg satte opp trygg innlogging for brukere med json web tokens i backend.</li>
                    <li>Jeg lagde funksjoner for  selecte kort, erstatte et kort med et annet tilfeldig et, og erstatte kort med et annet utvalg av kort.</li>
                    <li>Jeg satte opp backend dokumentasjon med SWAGGER.</li>
                    <li>Jeg lagde funksjon for  bookmarke et assessment scheme i backend, og lagde en knapp for  f det til  funke i frontend (noen andre fikk det til  se fint ut)</li>
                    <li>Jeg satte opp Protected Routes i frontend</li>
                    <li>Jeg lagde en turncounter komponent for lagversjonen av spillet, som viser hvem sin tur det er og hvor mange runder dere har spilt</li>
                </ul>
            </section>

            <section className="gallery-container">
                <h2>Galleri</h2>

                <div className="gallery">
                    <span className="gallery-image">
                        <img src={gamemode} alt="choose between playing solo or in groups" />
                        <span>Fr du starter spillet, velger du om du vil spille alene eller i gruppe.</span>
                    </span>
                    <span className="gallery-image">
                        <img src={missioncards} alt="select up to three mission cards before starting the game" />
                        <span>Du velger opp til 3 oppdragskort som du skal prve  oppfylle med vurderingsskjemaet ditt.</span>
                    </span>
                    <span className="gallery-image">
                        <img src={gameMain} alt="the game's main screen, where you select cards to replace or randomize" />
                        <span>Spill ved  flippe vurderingskort og erstatt dem.
                            Om du er alene kan du erstatte s mange du vil p en gang.
                            I gruppe kan hver spiller erstatte ett om gangen.
                        </span>
                    </span>
                    <span className="gallery-image">
                        <img src={replaceCard} alt="choose one of three cards of the same category to replace your selected card" />
                        <span>Nr du erstatter et kort fr du velge ett av tre kort fra samme kategori.</span>
                    </span>
                    <span className="gallery-image">
                        <img src={mySchemas} alt="a list of all the schemas you have made with their ratings" />
                        <span>Nr du er ferdig med spillet lagres skjemaet snn at du kan se p det senere og dele med andre.</span>
                    </span>
                    <span className="gallery-image">
                        <img src={exploreSchemas} alt="a list of all schemas made by other users, sorted by highest rating." />
                        <span>Du kan ogs se p andre sine skjemaer, gi dem en vurdering, og bookmarke de du liker best.</span>
                    </span>
                </div>
            </section>

        </div>
    )
};

export default SuperAssessor;